export default {
	name: 'basics',
	data() {
		return {
			hasLogin:false,
			animationSelect: '',
			currentSelectId: 0,
			carWashDesc:'',
			loading: false,
			currentSelectLong: '',
			currentSelectLat: '',
			currentSelectAddress: '',
			currentSelectCar: '',
			modalName: null,
			showQiPao: false,
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			currentSwiper: 0,
			indicatorDots: false,
			autoplay: false,
			interval: 2000,
			duration: 500,
			tarHeight: 50,
			isRight: true,
			screenHeight: 0,
			mapHeight: 500,
			contentheght: 110,
			title: 'map',
			latitude: 39.909,
			longitude: 116.39742,
			covers: [
				{
					id: 'dddds',
					latitude: 39.909,
					longitude: 116.39742,
					iconPath: '../../static/location.png',
					callout: {
						content: '无法服务，附近暂无技师',
						bgColor: '#d81e06',
						borderColor: '#d81e06',
						borderRadius: 50,
						display: 'ALWAYS',
						padding: 10,
						fontSize: 15,
						color: '#fff'
					}
				}
			],
			myCarList: [
				{
					cars: ['车辆1', '车辆2', '车辆3']
				},
				{
					cars: ['车辆4', '车辆5', '车辆6']
				}
			],
			selectCarList: [
				{
					id: 1,
					selectTag: '首洗优惠',
					selectImg: '../../static/logo.png',
					selectTitle: '外观',
					selectPrice: '15',
					desc:'加分加分流水的房客就沙发降到了附件是打开了附件是打开了就落空发送的记录'
				},
				{
					id: 2,
					selectTag: '首洗优惠',
					selectImg: '../../static/logo.png',
					selectTitle: '普洗',
					selectPrice: '25',
					desc:'就发的时刻均可发生六点尽量克服的是'
				},
				{
					id: 3,
					selectTag: '次卡专享价',
					selectImg: '../../static/logo.png',
					selectTitle: '精洗',
					selectPrice: '55',
					desc:'收到JFK了范德萨就开了放到数据库了JFK鲁大师 JFK老师的'
				},
				{
					id: 4,
					selectTag: '次卡专享价',
					selectImg: '../../static/logo.png',
					selectTitle: '打蜡',
					selectPrice: '99',
					desc:'撒JFK领导发动机萨洛克附近的萨科'
				}
			]
		};
	},
	onShow() {
		console.log('success');
	},

	onLoad() {
		let _this = this;
		uni.getStorage({
			key: 'hasLogin',
			success: function(res) {
				_this.hasLogin = res.data
			}
		});
		console.log("是否登录:",this.hasLogin)
		if(!this.hasLogin){
			uni.navigateTo({
				url: '/pages/user/login'
			}) 
		}
	},
	
	methods: {
		selectPromotion(id,index) {
			console.log("id："+id,"下标："+index);
			let that = this;
			this.carWashDesc = this.selectCarList[index].desc;
			this.currentSelectId = id;
			this.loading = false;
			setTimeout(function() {
				that.loading = true;
			}, 100);

			this.animationSelect = 'animation-shake';
			setTimeout(() => {
				that.animationSelect = 'shadow bg-white solids';
			}, 100);
		},
		hideModal(e) {
			this.modalName = null;
		},
		doCreateOrder(carNumber) {
			this.currentSelectCar = carNumber;
			this.modalName = 'ChooseModal';
			this.selectPromotion(2,1);
		},
		//跳转添加车辆页面
		toPages(url) {
			if(!this.hasLogin){
					url = '/pages/user/login'
			}
			uni.navigateTo({
				url: url
			});
		},
		//上一页
		preSwiper() {
			this.currentSwiper--;
		},
		//下一页
		nextSwiper() {
			this.currentSwiper++;
		},
		// 监听swiper 改变
		swiperChange(e) {
			console.log('当前:' + e.detail.current);
			console.log('原始:' + e.detail.source);
		},
		//选择车辆地址
		doSelectLocations() {
			let _this = this;
			uni.chooseLocation({
				success: function(res) {
					console.log('位置名称：' + res.name);
					console.log('详细地址：' + res.address);
					console.log('纬度：' + res.latitude);
					console.log('经度：' + res.longitude);
					_this.currentSelectAddress = res.name;
					_this.currentSelectLong = res.longitude;
					_this.currentSelectLat = res.latitude;
				}
			});
		},
		//选择车辆
		doSelectCars() {
			this.isRight = !this.isRight;
			if (this.isRight) {
				this.contentheght = 110;
			} else {
				this.contentheght = 220;
			}
			this.mapHeight = this.screenHeight - this.contentheght;
		}
	},
	mounted() {
		this.screenHeight = uni.getStorageSync('screenHeight') - 50 - uni.getStorageSync('bottomTarHeight');
		this.mapHeight = this.screenHeight - this.contentheght;
	}
};
